.container-charter{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 15px 0;
    width: 250px;
    max-height: 600px;
    background: rgb(73, 73, 73);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3) ;
    margin: 20px 0;
}
.container-charter:hover{
    background: #439A97;
}
.container-detail-character{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
}
.image-character{
    width: 180px;
    height: 180px;
}
.character-name{
    font-size: 1.1rem;
}
span{
    padding: 0 5px;
    font-size: 0.8rem;
}

