.container-title{
    width:100%;
    height: 50px;
    text-align: center;
}
.title-api{
    width:100%;
    height: 40px;
    font-size: 1.4rem;
}

/* responsive design */

@media screen and (min-width: 768px){
    .container-title{
        width:100%;
        height: auto;
        text-align: center;
    }
    .title-api{
        font-size: 2.5rem;
    }
}