:root{
    --color--verde:#439A97;
    --color--white: white;
    --color--backgrond: #282c34;
}
.container--home{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.container-search{
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.container--input_search{
    display: flex;
    justify-content: center;
    align-items: center;
}
.input_search{
    width: 200px;
    height: 30px;
    border: none;
    border-radius: 3px 0 0 3px;
    font-size: 0.9rem;
    padding-left: 10px ;
}
.container--pageView{
    font-size: .8rem;
}
.input--page__view{
    width: 50px;
    height: 30px;
    font-size: 0.8rem;
    background-color: var(--color--backgrond);
    color: var(--color--verde);
    text-align: center;
    border: none;
    /* border: 1px solid #439A97;; */
    border-radius: 8px;
}
.button_search{
    width:80px;
    height: 32px;
    border: none;
    background: var(--color--verde);
    color: var(--color--white);
    border-radius: 0 3px 3px 0;
    font-size: 0.9rem;
}
.container--buton__pages{
    width: 100%;
    height: auto;
    padding: 10px 0;
    display: flex;
    justify-content: space-around;
}
.button--page{
    width:80px;
    height: 30px;
    border: none;
    background: var(--color--verde);
    color: var(--color--white);
    font-size: .8rem;
    border-radius: 5px;
}
.footer--api{
    width: 100%;
    height: auto;
    padding: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.footer-pie{
    font-size: 1rem;
    color: var(--color--verde);
}
@media screen and (min-width: 760px) {
    
    .container--pageView{
        font-size: 1rem;
    }    
    .input--page__view{
        font-size: 1rem;
    }
    .button--page{
        width:100px;
        height: 40px;
        font-size: 1rem;
    }
}